footer {
  height: 299px;
  width: 100%;
  background-color: black;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

footer ul li {
  color: white;
  max-width: 188px;
  display: block;
  text-align: center;
}

footer ul {
  padding: 0;
  padding-inline-start: 0;
  padding-left: 0;
}

footer ul li:first-child {
  margin-block-start: 50px;
}

footer ul li:not(:first-child) {
  margin-block-start: 20px;
}

h2 {
  color: white;
  width: 900;
  font-size: 20px;
  font-family: Montserrat;
  text-transform: uppercase;
}

p {
  font-family: Montserrat;
  font-size: 16px;
  line-height: 19.5px;
  max-width: 419px;
  color: white;
  margin-block-start: 50px;
}

p span {
  font-weight: 700;
}

.navigationMenu {
  text-align: center;
}

.societyBlock {
  width: 329px;
}

.linkSociety {
  display: flex;
  gap: 62px;
  margin-block-start: 38px;
}

.linkSociety a {
  margin-inline: 0;
}

@media (max-width: 1224px) {
  .societyBlock {
    width: auto;
  }
}

@media (min-width: 1044px) {
  .infoBlock,
  .societyBlock,
  .navigationMenu {
    margin-block-start: 40px;
  }
}

@media (max-width: 900px) {
  footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: max-content;
    text-align: center;
  }

  footer div {
    margin-block-start: 50px;
  }

  footer ul li:first-child {
    margin-block-start: 15px;
  }

  .linkSociety {
    gap: 0px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-block: 10px;
  }

  footer div p {
    font-size: 16px;
    max-width: 300px;
  }
}
