.backgroundModal {
  width: 1520px;
  height: 658px;

  background-color: rgba(32, 32, 32, 0.85);
  filter: drop-shadow(0px 28px 48.4px rgba(0, 0, 0, 0.81));
  backdrop-filter: blur(6.500004291534424px);
  background-color: #202020da;
  position: relative;
}

.backgroundModal * {
  margin: 0;
  padding: 0;
}

.closeButton {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 33px;
  height: 33px;
  background-color: transparent;
}

.backgroundAll {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;

  background-color: rgba(0, 0, 0, 0.137);
  backdrop-filter: blur(15px);

  display: flex;
  justify-content: center;
  align-items: center;
}

.contentCard {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  gap: 21px;
}

.imageCard {
  width: 496px;
  height: 495px;
}

.dataCard {
  max-width: 528px;
  height: 495px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.dataCard h1 {
  color: #fff;
  font-family: Montserrat;
  font-size: 28px;
  font-style: normal;
  font-weight: 900;
  line-height: 25.884px; /* 121.9% */
  width: 100%;
  margin-block-end: 20px;
}

.description {
  color: #fff;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 25.256px;
  min-width: 100%;
}

.price {
  background-color: #4657b5;
  max-width: max-content;
  padding-inline: 16px;
  height: 41px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 10px;
}

.priceCard p:first-of-type {
  color: #fff;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19.504px; /* 121.9% */
  margin-block-end: 29px;
  margin-block-start: 100px;
}

.transation {
  display: flex;
  gap: 100px;
  margin-block-end: 45px;
}

.imageCard img {
  width: 100%;
  height: 100%;
}

.buyBtn {
  width: 494px;
  height: 62px;
  background: linear-gradient(
    225deg,
    #3d4eb0 0%,
    #4758b6 20.5%,
    #4959b7 24%,
    #3a4aa9 52.52%,
    #4b5bb5 74.5%,
    #4556b6 100%
  );
}

.inputConvert {
  background-color: #4657b5;
  width: 237px;
  height: 41px;
  border: none;
  outline: none;
  padding-inline-start: 10px;
  border-radius: 10px;
  color: #fff;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19.504px;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}
.buyBtn:disabled {
  background: #232c5b;
  cursor: default;
}

@media (max-width: 1600px) {
  .backgroundModal {
    width: 80%;
    padding-inline: 50px;
  }
}

@media (max-width: 1310px) {
  .transation {
    gap: 50px;
  }

  .contentCard {
    flex-direction: column;
  }

  .priceCard p:first-child {
    margin-block-start: 30px;
  }

  .backgroundModal {
    padding-block: 50px;
  }
}

@media (max-width: 570px) {
  .imageCard {
    max-width: 350px;
    text-align: center;
    display: flex;
    padding-inline: 20px;
    min-width: 250px;
    margin-block-start: 20px;
  }

  .dataCard h1 {
    font-size: 22px;
    line-height: 18.884px;
  }

  .description {
    font-size: 18px;
    line-height: 23px;
  }

  .backgroundModal {
    padding-block: 50px;
    max-width: 80vw;
  }

  .buyBtn {
    max-width: 450px;
    min-width: 250px;
  }

  .contentCard {
    max-width: 70%;
    align-items: center;
    justify-content: center;
    align-content: center;
    padding-inline: 20px;
    flex-direction: column;
    margin-inline: auto;
  }

  .transation {
    gap: 0px;
    flex-direction: column;
  }

  .priceCard p:first-of-type {
    color: #fff;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 19.504px; /* 121.9% */
    margin-block-end: 20px;
    margin-block-start: 20px;
  }
}

@media (max-width: 500px) {
  .backgroundModal {
    margin-inline: 20px;
    width: 100%;
    max-width: 90%;
    padding-inline: 0;
  }

  .dataCard h1 {
    font-size: 22px;
    line-height: 18.884px;
    max-width: 90%;
  }

  .description {
    font-size: 18px;
    line-height: 23px;
    max-width: 413px;
    min-width: 0;
  }

  .priceCard p:first-of-type {
    color: #fff;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 19.504px; /* 121.9% */
    margin-block-end: 20px;
    margin-block-start: 20px;

    max-width: 90%;
  }

  .inputConvert {
    max-width: 90%;
  }

  .buyBtn {
    max-width: 350px;
  }
}

@media (max-width: 400px) {
  .imageCard {
    max-width: 350px;
    text-align: center;
    display: flex;
    padding-inline: 20px;
    min-width: 150px;
    min-height: 0;
    margin-block-start: 5px;
    max-height: 50%;
    height: auto;
  }

  .backgroundModal {
    padding-inline: 10px;
    max-height: 80vh;
    max-width: 350px;
  }

  .dataCard {
    justify-content: start;
    gap: 10px;
  }

  .dataCard h1 {
    font-size: 18px;
    line-height: 15.884px;
    max-width: 90%;
    margin-block: 10px;
  }

  .description {
    font-size: 13px;
    line-height: 15px;
    max-width: 413px;
    min-width: 0;
  }

  .priceCard p:first-of-type {
    color: #fff;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 19.504px; /* 121.9% */
    margin-block-end: 10px;
    margin-block-start: 10px;

    max-width: 90%;
  }

  .inputConvert {
    max-width: 90%;
  }

  .transation {
    margin-block-end: 10px;
  }

  .buyBtn {
    max-width: 350px;
    margin-block-start: 20px;
  }
}
