.backgroundModal {
  position: relative;
  width: 493px;
  max-height: 831px;
  padding: 25px 15px;
  background-color: #202020;
  filter: drop-shadow(0px 32px 51px rgba(0, 0, 0, 0.42))
    drop-shadow(0px 4px 56px rgba(0, 0, 0, 0.25));
}

.backgroundComponent {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  max-width: 100%;

  background-color: rgba(0, 0, 0, 0.137);
  backdrop-filter: blur(15px);

  display: flex;
  justify-content: center;
  align-items: center;
}

.closeBtn {
  position: absolute;
  right: 20px;
  top: 10px;
  width: 33px;
  height: 33px;
  background-color: transparent;
}

.headerConext {
  color: #fff;
  font-family: Montserrat;
  font-size: 36px;
  font-style: normal;
  font-weight: 900;
  line-height: 43.884px; /* 121.9% */
  text-align: center;
  margin-block-start: 82px;
}

.serverSelect {
  width: 380px;
  height: 47px;
  position: relative;
}

.serverSelect input {
  width: 100%;
  height: 100%;
  background-color: #4657b5;
  border: none;
  outline: none;
  margin-block-start: 10px;
}

.decriptionSelector {
  margin-inline-start: 56px;
  margin-block-start: 91px;
  color: #fff;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19.504px; /* 121.9% */
}

.serverSelect input {
  padding-inline-start: 25px;
  color: #ffffff;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 19.504px; /* 121.9% */
}

.serverSelect input::placeholder {
  color: #afafaf;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 19.504px; /* 121.9% */
}

.descriptionNickname {
  margin-inline-start: 56px;
  margin-block-start: 44px;
  color: #fff;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19.504px; /* 121.9% */
}

.iconInput {
  position: absolute;
  right: -15px;
  top: 20px;
  bottom: 0;
}

.detaliesList li {
  display: block;
  margin-block-start: 10px;
  margin-inline-start: 16px;
  font-weight: normal;
  text-transform: none;
}

.payButton {
  text-align: center;
  display: flex;
  justify-content: center;
  margin-block-start: 150px;
  align-items: center;
  width: 250px;
  height: 50px;
  flex-shrink: 0;
}

.payButton:disabled {
  background-color: #232c5b;
  cursor: default;
}

@media (max-width: 760px) {
  .closeBtn {
    max-width: 21px;
    max-height: 21px;
  }

  .headerConext {
    font-size: 20px;
    text-align: center;
  }

  .decriptionSelector,
  .descriptionNickname:not(:last-of-type) {
    font-size: 14px;
    text-align: center;
    margin-inline: 0;
    margin-block-start: 50px;
  }

  .flexContainer {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .serverSelect {
    margin-inline: 0;
  }

  .payButton {
    margin-block-start: 50px;
  }

  .backgroundModal {
    padding: 25px 15px;
  }
}

@media (max-width: 525px) {
  .backgroundModal {
    max-width: 90%;
    margin-inline: 50px;
  }

  .serverSelect {
    position: relative;
  }

  .serverSelect input {
    max-width: 80%;

    display: flex;
    margin-inline: auto;
  }

  .iconInput {
    position: absolute;
    right: 40px;
  }

  .detaliesList li,
  .detaliesList {
    padding-inline-start: 0;
  }

  .descriptionNickname:last-of-type {
    padding-inline-start: 0;
    margin-inline-start: 16px;
  }
}
