@media (max-width: 1044px) {
  .containerMenu:first-of-type {
    padding-block-start: 50px;
  }

  ul li {
    display: block;
  }

  .containerMenu {
    position: fixed;
    height: 100vh;
    top: 0;
    width: 300px;
    left: -550px;
    background: #171717eb;
    box-shadow: 7px 0px 7.6px 0px #00000040;
    transition: 0.5s;
    flex-direction: column;
    justify-content: start;
    flex-wrap: nowrap;
    z-index: 1;
  }

  .containerOpen {
    transform: translateX(550px);
    z-index: 1;
  }

  .containerMenu ul li {
    margin-block: 29.5px;
    margin-inline: max-content;
    max-width: max-content;
    text-align: left;
    flex-wrap: nowrap;
  }

  .containerMenu ul li a {
    margin-inline: max-content;
    max-width: max-content;
  }

  .activeButton {
    position: absolute;
    top: 10px;
    left: -80px;
    transform: rotate(90deg);
    transition: 1s;
    background-color: transparent;
    z-index: 10;
    user-select: none;
  }

  .activeButton:focus,
  .activeButton:focus {
    outline: none;
    box-shadow: none;
    border: none;
    user-select: none;
  }

  nav button:first-child {
    display: block;
    background-color: transparent;

    position: fixed;
    top: 10px;
    left: -80px;
    transition: 1s;
  }

  button:focus,
  button:active {
    outline: none;
    box-shadow: none;
    border: none;
    user-select: none;
  }

  nav button:last-child {
    margin-block: 29.5px;
    margin-inline: 21.5px;
  }

  .imageMenu,
  .imageMenu:active,
  .imageMenu:focus,
  button,
  button:active,
  button:focus {
    user-select: none;
  }

  nav img:not(:first-child) {
    position: absolute;
    max-width: 117px;
    top: -13px;
    left: 0;
    right: 0;
  }

  nav {
    width: 100%;
  }
}

@media (min-width: 1044px) {
  nav button:first-child {
    display: none;
  }

  nav {
    max-width: 1525px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    text-align: center;
    align-self: center;
    left: 0;
    right: 0;
  }

  .containerMenu button {
    margin-inline-start: 0;
    margin-inline-end: 33px;
  }

  .containerMenu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-inline: 0;
    width: 100%;
  }
}
