.navBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: absolute;
}

ul li {
  display: inline;
  margin-inline-start: 21.5px;
  margin-inline-end: 21.5px;
  font-family: Montserrat;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  flex-wrap: nowrap;
  list-style-type: none;
}

li {
  cursor: pointer;
  color: white;
}

button {
  width: 236px;
  height: 46px;
  font-family: Montserrat;
  font-weight: 700;
  font-size: 12px;
  background: #4959b7;
  color: white;
  border: none;
  text-transform: uppercase;
  cursor: pointer;
}

.recoverBtn {
  width: 236px;
  height: 46px;
  font-family: Montserrat;
  font-weight: 700;
  font-size: 12px;
  background: #4959b7;
  color: white;
  border: none;
  text-transform: uppercase;
  cursor: pointer;
}

.logoSetting {
  cursor: pointer;
}

a.active {
  border-bottom: white 2px solid;
  height: 30px;
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  color: rgba(255, 255, 255, 0.619);
}

button {
  user-select: none;
}
