* {
  margin-inline: auto;
}

.shopHeader {
  font-family: Montserrat;
  font-size: 30px;
  font-weight: 900;
  line-height: 36.57px;
}

.mainContext {
  padding-block-start: 170px;
  padding-block-end: 100px;
  background: linear-gradient(
    225deg,
    #000000 0%,
    #193a57 0%,
    #1d3952 51.19%,
    #0c2940 99.73%
  );
  margin-inline: auto;
}

.containerItems {
  max-width: 1520px;
  margin-block-start: 140px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(495px, 495px));
  grid-template-rows: repeat(auto-fill, 365px);
  column-gap: 17px;
  justify-content: center;
}

.cardItem {
  text-align: center;
  width: 494px;
  height: 338px;
  margin-block-end: 38px;
}

.cardItem h2 {
  margin-block-end: 10px;
}

.containerDo {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.containerDo button,
.containerDo p,
.containerDo span {
  margin-inline: 0;
}

.containerDo button {
  width: 314px;
}

.containerDo p {
  display: flex;
  justify-content: center;
  gap: 5px;
  align-items: center;
  margin: 0;
  width: 170px;
  height: 50px;
  background: #161616;
}

.containerDo span {
  font-weight: 400;
}

.cardItem img {
  min-width: 344px;

  max-width: 494px;
}

@media (max-width: 1024px) {
  .headerStart {
    font-size: 24px;
    max-width: 800px;
    min-width: 400px;
  }

  .mainContext {
    padding-block-start: 1px;
  }
}

@media (max-width: 768px) {
  .containerItems {
    margin-block-start: 0px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(322px, 495px));
    grid-template-rows: repeat(auto-fill, 322px);
    justify-content: center;
  }

  .headerStart {
    font-size: 14px;
    padding-inline: 10px;
    min-width: 1px;
    max-width: 90%;
  }

  .cardItem {
    margin-block-end: 19px;
    height: 250px;
    max-width: 322px;
  }

  .cardItem img {
    max-width: 322px;
    width: 322px;
    min-width: 322px;
  }

  .containerDo {
    max-width: 322px;
  }

  .cardItem button {
    max-width: 205px;
    max-height: 33px;
  }

  .containerDo p {
    max-width: 111px;
    font-size: 10px;
    max-height: 33px;
  }
}
