.backgroundRecover {
  max-width: 100%;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.137);
  backdrop-filter: blur(15px);
  position: fixed;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;
}

.modalForm {
  background: #303030e9;
  box-shadow: 0px 0px 39px 0px #00000075;

  padding-inline: 80px;
  padding-block: 50px;
  max-width: 100%;
}

.emailInput {
  display: flex;
  justify-content: center;
}

.closeModal {
  background-color: transparent;
  color: white;
  width: 33px;
  height: 33px;
  position: absolute;
  right: -15px;
  top: -15px;
}
.modalForm button img {
  width: 33px;
  height: 33px;
}

.modalForm h1 {
  color: white;
  text-transform: uppercase;
  font-style: normal;
  font-family: Montserrat;
  font-size: 48px;
  font-weight: 700;
  line-height: 58.51px;
  text-align: center;
}

.headerText {
  color: black;
}

.closeForm {
  width: 100%;
  height: 33px;
  position: relative;
}

.modalContent {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.modalContent img {
  width: 478px;
  height: 478px;
}

.line {
  width: 100%;
  height: 15px;
  background-color: #3e4eac;
  border-radius: 15px;
}

.emailInput {
  margin-block: 45px;
  width: 376px;
  height: 27px;
  background-color: #4657b5;
  border: none;
  outline: none;
  padding-inline: 15px;
  padding-block: 13px;
  font-size: 18px;
  color: white;
  text-align: center;
}

.emailInput::placeholder {
  color: rgba(255, 255, 255, 0.662);
}

.InputContainer {
  width: 100%;
  display: flex;
  justify-content: center;
}

.recoverButton {
  padding-inline: 76px;
  padding-block: 25px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 1043px) {
  .modalContent p {
    max-width: 100%;
    padding-inline: 50px;
  }

  .modalContent img {
    display: none;
  }

  .modalContent p:last-of-type {
    display: none;
  }

  .modalForm {
    max-width: 80%;
  }

  .line {
    margin-inline: 50px;
    margin-block-start: 50px;
    margin-block-end: 50px;
    height: 3px;
  }

  .backgroundRecover {
    max-width: 100%;
  }
}

@media (max-width: 680px) {
  .modalForm {
    padding-inline: 100px;
    max-width: 50%;
  }

  .modalForm h1 {
    font-family: Montserrat;
    font-size: 20px;
    font-weight: 900;
    line-height: 25px;
    text-align: center;
  }

  .modalContent p {
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 700;
    line-height: 21.94px;
    text-align: center;
    width: 100%;
    padding: 0;
  }

  .line {
    width: 80%;
    height: 4px;
    margin-inline: auto;
  }

  .emailInput {
    width: 273px;
    height: 40px;
    top: 439px;
    left: 78px;
    gap: 0px;
  }

  .closeModal {
    background-color: transparent;
    color: white;
    width: 33px;
    height: 33px;
    position: absolute;
    right: -80px;
    top: -40px;
  }
}

@media (max-width: 460px) {
  .modalForm {
    padding-inline: 0;
    margin-inline: 0;
    min-width: 350px;
  }

  .line {
    margin-block: 30px;
  }

  .emailInput {
    display: flex;
    justify-content: center;
  }

  .closeModal {
    right: 20px;
    top: -30px;
  }
}
