.headerShop {
  background: linear-gradient(
    45deg,
    rgb(69, 91, 146) 0%,
    rgb(69, 101, 185) 47.818%,
    rgb(73, 97, 158) 100%
  );
  color: rgb(255, 255, 255);
  font-family: Montserrat;
  font-style: normal;
  font-size: 36px;
  font-weight: 900;
  line-height: 43.88px;
  letter-spacing: 0px;
  text-transform: uppercase;
  text-align: center;
  max-width: 1007px;
  height: 85px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-block-start: 100px;
  padding-inline: 50px;
}

@media (max-width: 550px) {
  .headerShop {
    font-size: 24px;
    line-height: 28px;
  }
}

@media (max-width: 400px) {
  .headerShop {
    font-size: 18px;
    line-height: 24px;
  }
}
